a {
  color: white;
  text-decoration: none;
}

* {
  padding: 0;
  margin: 0;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  box-sizing: border-box;
  scroll-behavior: smooth;
  transition: transform, opacity 450ms;
}

body {
  /* -webkit-font-smoothing: antialiased; */
  -moz-osx-font-smoothing: grayscale;
}

.absolute {
  position: absolute;
}

.full-size {
  width: 100%;
  height: 100%;
}

.social-media-item {
  width: 50px;
  height: 50px;
}

li {
  list-style: none;
}

.circle {
  border-radius: 50%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.dark-background {
  background: black !important;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.5);
  top: 0;
}

.light-background {
  background-color: white !important;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.5);
}

.stick-top {
  top: 0 !important;
}

.absolute-center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.page {
  position: relative;
}

.custom-button {
  background-color: transparent;
  border: none;
}

.btn {
  cursor: pointer;
}

.center-child {
  display: flex;
  justify-content: center;
  align-items: center;
}

.display-none {
  display: none !important;
}

.page-name {
  z-index: 2000;
  position: relative;
  left: 50px;
  top: 80px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  max-width: 100%;
  text-overflow: clip;
  font-style: italic;
  font-weight: 100;
  font-size: 15pt;
}

.page-name::before {
  content: "";
  position: absolute;
  height: 2px;
  width: 100px;
  background: white;
  left: -20px;
  top: -3px;
}

.page-name-position {
  position: absolute;
  top: 0vh;
  left: 10px;
}

.hide {
  display: none !important;
}

.white-text {
  color: white;
}

@media (max-width: 768px) {
  .page-name-position {
    position: absolute;
    top: -8vh;
    left: 10px;
  }

  .collapse-on-mobile {
    display: none !important;
    width: 0 !important;
    height: 0 !important;
  }
}

.about-us {
  max-height: 200vh;
  width: 100%;
  background-color: #1618af;
  padding: 90px 20px;
  position: relative;
  color: white;
}

.about-us .content {
  padding: 50px 20px 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  z-index: 1;
  background-color: white;
  color: black;
}

.about-us .content .marginated {
  margin: 0 0.5vw 50px;
}

.about-us .content .title {
  text-align: center;
}

.about-us .content .text {
  line-height: 24px;
}

.about-us button {
  padding: 15px 30px;
  background-color: #1618af;
  color: white;
  margin: 10px 0;
  border-radius: 2px;
  border: none;
}

.about-us:after {
  content: "";
  position: absolute;
  z-index: 0;
  top: -70px;
  left: 0;
  border-bottom: 70px solid #1618af;
  border-right: 98.5vw solid transparent;
}

@media (min-width: 1000px) {
  .about-us .page-name-position {
    position: absolute;
    top: -2vh;
    left: 5px;
  }
  .about-us .content {
    width: 50% !important;
    padding: 5% 2%;
    margin: auto;
  }
  .about-us .content .marginated {
    margin: 0 2vw 10% !important;
  }
  .about-us .content .marginated:nth-child(3) {
    margin-bottom: 0 !important;
  }
}
