.laptop-view-container {
  height: 400px;
  z-index: 100;
  max-height: 500px;
  position: fixed;
  align-items: center;
  top: 50%;
  transform: translateY(-50%);
  right: 10px;
  padding: 15px 0;
  overflow: hidden;
  z-index: 1001;
}

.laptop-view-container .creator {
  display: none;
  width: 0;
  height: 0;
}

.laptop-view-container .social-media-list {
  border-radius: 50px;
  height: 100%;
  padding: 20% 10%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.laptop-view-container .social-media-item {
  transform: scale(0.8);
}

.laptop-view-container .facebook {
  padding-left: 12.5px;
}

.phone-view-container,
.last-scroll {
  position: relative;
  display: flex;
  bottom: 0;
}

.phone-view-container .creator,
.last-scroll .creator {
  background-color: antiquewhite;
  padding: 70px 0;
  width: 50%;
  text-align: center;
  order: 1;
  overflow: hidden;
}

.phone-view-container .creator p,
.last-scroll .creator p {
  font-size: 19pt;
  font-family: "Times New Roman", Times, serif;
  margin-bottom: 30px;
}

.phone-view-container .creator div,
.last-scroll .creator div {
  transform: scale(0.8);
}

@media (max-width: 350px) {
  .phone-view-container .creator div,
  .last-scroll .creator div {
    transform: scale(0.7) translateX(-10px);
  }
}

.phone-view-container .social-media-list,
.last-scroll .social-media-list {
  display: flex;
  flex-direction: row;
  padding: 50px 0;
  width: 50%;
  justify-content: space-around;
  align-items: center;
}

.phone-view-container .social-media-list .facebook,
.last-scroll .social-media-list .facebook {
  transform: scale(1.1) translate(5px, 3px);
}

@media (max-width: 768px) {
  .phone-view-container,
  .last-scroll {
    flex-direction: column;
  }
  .phone-view-container .creator,
  .last-scroll .creator {
    order: 0;
    width: 100%;
  }
  .phone-view-container .social-media-list,
  .last-scroll .social-media-list {
    width: 100%;
  }
}
